.footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
}
.f-content{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
}
.f-iocns{
    display: flex;
    gap: 2rem;
}
@media screen and (max-width: 480px){
    .f-content{
        transform: scale(0.5);
    }
}